import { useLocation } from "react-router-dom";
import { PriceBox, PriceWrap } from "../styles/Coin.styled";

interface RouterState {
  id: string;
  name: string;
  symbol: string;
  rank: number;
  circulating_supply: number;
  total_supply: number;
  max_supply: number;
  beta_value: number;
  first_data_at: string;
  last_updated: string;
  quotes: {
    USD: {
      ath_date: string;
      ath_price: number;
      market_cap: number;
      market_cap_change_24h: number;
      percent_change_1h: number;
      percent_change_1y: number;
      percent_change_6h: number;
      percent_change_7d: number;
      percent_change_12h: number;
      percent_change_15m: number;
      percent_change_24h: number;
      percent_change_30d: number;
      percent_change_30m: number;
      percent_from_price_ath: number;
      price: number;
      volume_24h: number;
      volume_24h_change_24h: number;
    };
  };
}
export default function Price() {
  const location = useLocation();
  const coinPrice = location.state as RouterState;
  const coinPriceInfo = coinPrice.quotes.USD;
  return (
    <>
      <PriceWrap>
        <PriceBox>현재 금액 : {coinPriceInfo.price.toFixed(1)} USD</PriceBox>
        <PriceBox>volume_24h : {coinPriceInfo.volume_24h.toFixed(3)}</PriceBox>
        <PriceBox>change_24h : {coinPriceInfo.volume_24h_change_24h}</PriceBox>
        <PriceBox>
          15분 대비 가격변동 : {coinPriceInfo.percent_change_15m}%p
        </PriceBox>
        <PriceBox>
          30분 대비 가격변동 : {coinPriceInfo.percent_change_30m}%p
        </PriceBox>
        <PriceBox>
          1시간 대비 가격변동 : {coinPriceInfo.percent_change_1h}%p
        </PriceBox>
        <PriceBox>
          6시간 대비 가격변동 : {coinPriceInfo.percent_change_6h}%p
        </PriceBox>
        <PriceBox>
          12시간 대비 가격변동 : {coinPriceInfo.percent_change_12h}%p
        </PriceBox>
        <PriceBox>
          1일 대비 가격변동 : {coinPriceInfo.percent_change_24h}%p
        </PriceBox>
        <PriceBox>
          7일 대비 가격변동 : {coinPriceInfo.percent_change_7d}%p
        </PriceBox>
        <PriceBox>
          30일 대비 가격변동 : {coinPriceInfo.percent_change_30d}%p
        </PriceBox>
        <PriceBox>
          1년 대비 가격변동 : {coinPriceInfo.percent_change_1y}%p
        </PriceBox>
      </PriceWrap>
    </>
  );
}
