import { useEffect, useState } from "react";
import { Link, useMatch } from "react-router-dom";
import { Chart as ChartJS } from "chart.js/auto";

import { useLocation, Outlet, useParams } from "react-router-dom";

import styled from "styled-components";
import {
  Content,
  HeaderItem,
  Helmet,
  Tap,
  TapWrap,
  TextMid,
} from "../styles/Coin.styled";

const Container = styled.div`
  padding: 0px 20px;
  display: flex;
  flex-direction: column;
  max-width: 700px;
  margin: auto;
  align-items: center;
`;
const Header = styled.div`
  display: flex;
  justify-content: space-between;

  align-items: center;
  margin: 10px 0;
  width: 100%;
  max-width: 500px;
`;
const Title = styled.h1`
  font-size: 48px;
  color: ${(props) => props.theme.primaryColor};
  margin-bottom: 20px;
`;
const Loader = styled.span`
  font-size: 27px;
`;
const Image = styled.img`
  width: 35px;
  height: 35px;
  margin-right: 10px;
`;

interface RouterState {
  name: string;
  id: string;
}
interface InfoData {
  id: string;
  name: string;
  symbol: string;
  rank: number;
  is_new: boolean;
  is_active: boolean;
  type: string;
  description: string;
  message: string;
  open_source: boolean;
  started_at: string;
  development_status: string;
  hardware_wallet: boolean;
  proof_type: string;
  org_structure: string;
  hash_algorithm: string;
  first_data_at: string;
  last_data_at: string;
}

interface PriceData {
  id: string;
  name: string;
  symbol: string;
  rank: number;
  circulating_supply: number;
  total_supply: number;
  max_supply: number;
  beta_value: number;
  first_data_at: string;
  last_updated: string;
  quotes: {
    USD: {
      ath_date: string;
      ath_price: number;
      market_cap: number;
      market_cap_change_24h: number;
      percent_change_1h: number;
      percent_change_1y: number;
      percent_change_6h: number;
      percent_change_7d: number;
      percent_change_12h: number;
      percent_change_15m: number;
      percent_change_24h: number;
      percent_change_30d: number;
      percent_change_30m: number;
      percent_from_price_ath: number;
      price: number;
      volume_24h: number;
      volume_24h_change_24h: number;
    };
  };
}

export default function Coin() {
  const [loading, setLoading] = useState<boolean>(true);
  //const { coinId } = useParams();
  const [coinInfo, setCoinInfo] = useState<InfoData>();
  const [coinPrice, setCoinPrice] = useState<PriceData>();
  const location = useLocation();
  const coin = location.state as RouterState;
  const priceMatch = useMatch("/:coinId/price");
  const chartMatch = useMatch("/:coinId/chart");
  console.log(coin);
  useEffect(() => {
    (async () => {
      const info = await (
        await fetch(`https://api.coinpaprika.com/v1/coins/${coin.id}`)
      )?.json();
      const price = await await (
        await fetch(`https://api.coinpaprika.com/v1/tickers/${coin.id}`)
      )?.json();
      //   console.log(price);
      setCoinInfo(info);
      setCoinPrice(price);
      setLoading(false);
    })();
  }, []);

  return (
    <Container>
      <Header>
        <Title>
          코인 : {coin?.name ? coin.name : loading ? "Loading" : coinInfo?.name}
        </Title>
      </Header>
      {loading ? (
        <Loader>loading ...</Loader>
      ) : (
        <Container>
          <Helmet>
            <HeaderItem>
              <TextMid>rank : </TextMid>
              <TextMid>{coinInfo?.rank}</TextMid>
            </HeaderItem>
            <HeaderItem>
              <TextMid>symbol : </TextMid>
              <TextMid>{coinInfo?.symbol}</TextMid>
            </HeaderItem>
            <HeaderItem>
              <TextMid>open source : </TextMid>
              <TextMid>4</TextMid>
            </HeaderItem>
          </Helmet>
          <Content>
            <TextMid>{coinInfo?.description}</TextMid>
          </Content>
          <Helmet>
            <HeaderItem>
              <TextMid>Total Supply : </TextMid>
              <TextMid>{coinPrice?.total_supply}</TextMid>
            </HeaderItem>
            <HeaderItem>
              <TextMid>price : </TextMid>
              <TextMid>{coinPrice?.quotes.USD.price.toFixed(1)}</TextMid>
            </HeaderItem>
          </Helmet>
          <TapWrap>
            <Tap isActive={chartMatch ? true : false}>
              <Link to={`/${coinInfo?.name}/chart`} state={coinInfo}>
                chart
              </Link>
            </Tap>
            <Tap isActive={priceMatch ? true : false}>
              <Link to={`/${coinInfo?.name}/price`} state={coinPrice}>
                price
              </Link>
            </Tap>
          </TapWrap>
          <Outlet />
        </Container>
      )}
    </Container>
  );
}
