import { useState } from "react";
import { Bar } from "react-chartjs-2";
// import { Chart as ChartJS } from "chart.js/auto";
// import Chart from "chart.js/auto";
import "chart.js/auto";
import { draw, generate } from "patternomaly";
import { useLocation } from "react-router-dom";
import { darkTheme } from "../Theme";
interface DataSetProps {
  data: object[];
  borderColor: string;
  backgroundColor: string;
  type: string;
  label: string;
  borderWidth: number;
}
interface RouterState {
  id: string;
  name: string;
  symbol: string;
  rank: number;
  is_new: boolean;
  is_active: boolean;
  type: string;
  description: string;
  message: string;
  open_source: boolean;
  started_at: string;
  development_status: string;
  hardware_wallet: boolean;
  proof_type: string;
  org_structure: string;
  hash_algorithm: string;
  tags: any;
  first_data_at: string;
  last_data_at: string;
}
interface DataProps {
  labels?: string;
  datasets?: object[];
}
interface coinTagProps {
  id: string;
  name: string;
  coin_counter: number;
  ico_counter: number;
}

export default function ChartProps() {
  const location = useLocation();
  const coinInfo = location.state as RouterState;

  const [coinData, setCoinData] = useState({
    labels: coinInfo.tags.map((item: coinTagProps) => item.name),
    datasets: [
      {
        label: "Coin Counter",
        data: coinInfo.tags.map((item: coinTagProps) => item.coin_counter),
        backgroundColor: [
          "#0984e3",
          "#f38b4a",
          "#56d798",
          "#ff8397",
          "#6970d5",
          "#81ecec",
          "#55efc4",
          "#fdcb6e",
          "#636e72",
        ],
        hoverBackgroundColor: [
          "#74b9ff",
          "#f38b4a",
          "#56d798",
          "#ff8397",
          "#6970d5",
          "#81ecec",
          "#55efc4",
          "#fdcb6e",
          "#636e72",
        ],
        boderWidth: 2,
        color: darkTheme.textColor,
      },
    ],
    options: {
      responsive: false,
      scales: {
        xAxes: [
          {
            ticks: {
              fontColor: "rgba(12, 13, 13, 1)",
              fontSize: 14,
            },
            gridLines: {
              color: "rgba(87, 152, 23, 1)",
              lineWidth: 3,
            },
          },
        ],
      },
      plugins: {
        legend: {
          labels: {
            // This more specific font property overrides the global property
            font: {
              size: 14,
              color: darkTheme.textColor,
            },
          },
        },
        colors: {
          forceOverride: true,
        },
      },
    },
  });

  console.log(coinData);

  return (
    <div>
      <div style={{ paddingTop: "30px", paddingBottom: "50px" }}>
        <Bar data={coinData} width="500" height="300" />
      </div>
    </div>
  );
}
